.menu{
    display: flex;
    li{
        padding: 16px 18px 16px 20px;
    }
    &.menu-min{
        .dropdown{
            li{
                a{
                    white-space: nowrap;
                }
            }
        }
    }
    .dropdown{
        z-index: 3;
    }
}
.dropdown{
    position: absolute;
    top: 100%;
    left: 0;
    border-radius: 10px;
    z-index: 2;
    background-color: var(--White);
    padding: 20px 27px;
    min-width: 165px;
    display: none;
    box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, .15);
    li{
        padding: 11px 0;
    }
    .dropdown{
        left: 100%;
        top: 0;
    }
}